import Vue from 'vue';
import Vuex from 'vuex';
import clone from '@/assets/lib/clone';
import createID from '@/assets/lib/createID';

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    recordList: [],
    tagList: [],
    currentTag: undefined
  } as RootState,


  mutations: {
    fetchRecords(state) {
      state.recordList = JSON.parse(window.localStorage.getItem('recordList') || '[]');
      if(!state.recordList|| state.recordList.length===0){
        const record1: RecordItem = {"tags":[{"id":"1","name":"Food"}],"notes":"","type":"-","amount":10,"createdAt":"2020-12-31"}
        const record2: RecordItem = {"tags":[{"id":"2","name":"Cloth"}],"notes":"","type":"-","amount":10,"createdAt":"2020-12-31"}
        const record3: RecordItem = {"tags":[{"id":"1","name":"Food"}],"notes":"","type":"-","amount":20,"createdAt":"2020-12-30"}
        const record4: RecordItem = {"tags":[{"id":"2","name":"Cloth"}],"notes":"","type":"-","amount":40,"createdAt":"2020-12-29"}
        const record5: RecordItem = {"tags":[{"id":"1","name":"Food"}],"notes":"","type":"-","amount":35,"createdAt":"2020-12-28"}
        const record6: RecordItem = {"tags":[{"id":"1","name":"Food"}],"notes":"","type":"-","amount":25,"createdAt":"2020-12-27"}
        const record7: RecordItem = {"tags":[{"id":"1","name":"Food"}],"notes":"","type":"-","amount":46,"createdAt":"2020-12-26"}
        const record8: RecordItem = {"tags":[{"id":"1","name":"Food"}],"notes":"","type":"-","amount":180,"createdAt":"2020-12-25"}
        const record9: RecordItem = {"tags":[{"id":"1","name":"Food"}],"notes":"","type":"-","amount":0,"createdAt":"2020-12-24"}
        store.commit('createRecord', record1)
        store.commit('createRecord', record2)
        store.commit('createRecord', record3)
        store.commit('createRecord', record4)
        store.commit('createRecord', record5)
        store.commit('createRecord', record6)
        store.commit('createRecord', record7)
        store.commit('createRecord', record8)
        store.commit('createRecord', record9)
        const record11: RecordItem = {"tags":[],"notes":"","type":"+","amount":100,"createdAt":"2020-12-31"}
        const record12: RecordItem = {"tags":[],"notes":"","type":"+","amount":110,"createdAt":"2020-12-31"}
        const record13: RecordItem = {"tags":[],"notes":"","type":"+","amount":0,"createdAt":"2020-12-30"}
        const record14: RecordItem = {"tags":[],"notes":"","type":"+","amount":101,"createdAt":"2020-12-29"}
        const record15: RecordItem = {"tags":[],"notes":"","type":"+","amount":350,"createdAt":"2020-12-28"}
        const record16: RecordItem = {"tags":[],"notes":"","type":"+","amount":251,"createdAt":"2020-12-27"}
        const record17: RecordItem = {"tags":[],"notes":"","type":"+","amount":460,"createdAt":"2020-12-26"}
        const record18: RecordItem = {"tags":[],"notes":"","type":"+","amount":180,"createdAt":"2020-12-25"}
        const record19: RecordItem = {"tags":[],"notes":"","type":"+","amount":0,"createdAt":"2020-12-24"}
        store.commit('createRecord', record11)
        store.commit('createRecord', record12)
        store.commit('createRecord', record13)
        store.commit('createRecord', record14)
        store.commit('createRecord', record15)
        store.commit('createRecord', record16)
        store.commit('createRecord', record17)
        store.commit('createRecord', record18)
        store.commit('createRecord', record19)
      }
    },
    createRecord(state,record){
      const deepCloneRecord: RecordItem = clone(record);
      deepCloneRecord.createdAt =  deepCloneRecord.createdAt || new Date().toISOString();
      state.recordList?.push(deepCloneRecord);
      store.commit('saveRecords')
    },
    saveRecords(state) {
      window.localStorage.setItem('recordList',
          JSON.stringify(state.recordList));
    },
    fetchTags(state){
      state.tagList = JSON.parse(window.localStorage.getItem('tagList') || '[]')
      if(!state.tagList || state.tagList.length===0){
        store.commit('createTag', 'Food')
        store.commit('createTag', 'Cloth')
        store.commit('createTag', 'TPT')
      }
    },
    createTag(state,name: string) {
      const names = state.tagList.map(item => item.name);
      if (names.indexOf(name) >= 0) {
        window.alert('Can not create duplicated tags');
        return 'duplicated'
      }
      if (name === ' ') {
        window.alert('Can not create empty tags');
        return 'empty'
      }
      const id = createID().toString();
      state.tagList.push({id: id, name: name});
      store.commit('saveTags');
      return 'success';

    },
    saveTags(state) {
      window.localStorage.setItem('tagList', JSON.stringify(state.tagList));
    },
    removeTag(state,id: string) {
      let index = -1;
      for (let i = 0; i < state.tagList.length; i++) {
        if (state.tagList[i].id === id) {
          index = i;
          break;
        }
      }
      state.tagList.splice(index, 1);
      store.commit('saveTags')
    },
    findTag(state,id: string) {
      state.currentTag = state.tagList.filter(t => t.id === id)[0];
    },
    updateTag(state, payload: { id: string;name: string }) {
      const {id, name} = payload;
      const idList = state.tagList.map(item => item.id);
      if (idList.indexOf(id) >= 0) {
        const names = state.tagList.map(item => item.name);
        if (names.indexOf(name) >= 0) {
          window.alert('Can not create duplicated tags');
        } else {
          const tag = state.tagList.filter(item => item.id === id)[0];
          tag.name = name;
          store.commit('saveTags')
        }
      }
    },
  },
  actions: {
  },
  modules: {
  }
})

export  default store
